import * as React from 'react'

export default ({ data }) => (
  <div>
    <h1>
      About {data.site.siteMetadata.siteName}
    </h1>
    <p>
      {'This is a gatsby starter for you to build a awesome blog with typescript!'}
    </p>
  </div>
)

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        siteName
      }
    }
  }
`



